import React, {useState, useEffect} from "react";
import { Bar, Bubble } from "react-chartjs-2";


const chartStyle = {
  width: '100%'
};

const labels = ["Jan", "Feb", "Mar", "Apr"]


export default function BarChart() {

  return (
    <div style={chartStyle}>
      <Bar
        data={{
          labels: labels,
          datasets: [
            {
              label: "Monthly Revenue",
              data: [200, 300, 400, 150],
            },
          ],
        }}
        options={{}}
      />
    </div>
  );
}

export function BubbleChart() {

  return (
    <div style={chartStyle}>
      <Bubble
        data={{
          labels: labels,
          xLabels: "Months",
          yLabels: "Revenue",
          datasets: [{
            label: "Monthly Revenue",
            data: [ 
              {x: 1, y: 200, r: 15}, 
              {x: 2, y: 300, r: 20}, 
              {x: 3, y: 400, r: 25}, 
              {x: 4, y: 150, r: 10},
            ]
          }]
        }}
        options={{}}
      />
    </div>
  );
}

export function HorizontalChart(props) {
  const [xAxis, setXAxis] = useState([]);
  const [yAxis, setYAxis] = useState([]);

  const {state} = props;
  useEffect(()=>{
    if(state.queryResults.hasOwnProperty("query1") ){
      console.log("It's coming ", state.queryResults.hasOwnProperty("query1") )
      const tempxAxis = state.queryResults.query1.result.map( (value)=> value[0]);
      setXAxis(tempxAxis)
      const tempyAxis = state.queryResults.query1.result.map( (value)=> value[1]);
      setYAxis(tempyAxis)
    }
  },[state])
  console.log(xAxis, yAxis)
  return (
    <div style={chartStyle}>
      <Bar
        data={{
          labels: xAxis,
          datasets: [
            {
              label: "Computer Vulnerabilities",
              data: yAxis,
            },
          ],
        }}
        options={{
          indexAxis: "y"
        }}
      />
    </div>
  );
}


