import Layout from './Components/Layout'
import React, { useReducer, useEffect } from 'react'
import Chart from 'chart.js/auto'; // Keep this import - it's internally necessary for react-chartjs-2 to register the Category scale globally.
import { asyncQueue } from './Utility/utility';

export const ACTIONS = {
  SET_QUERIES_DATA: 'set_queries_data',
}

const initialState = { 
  queryResults: {
    query0:{
      query: "This is Test Query",
      result: [
        [
            "SRV2",
            2219
        ],
        [
            "JBOX01",
            2226
        ],
        [
            "rhel801-thehopswarm-lcl",
            492
        ],
        [
            "VMBFE01",
            3073
        ],
        [
            "UBOX01",
            2372
        ],
        [
            "Mikes2017Mac",
            848
        ],
        [
            "TBOX01",
            2316
        ]
    ]
    }
  }
}


export function reducer(state, action) {
  switch (action.type) {
    case ACTIONS.SET_QUERIES_DATA:
      return {
        ...state,
        queryResults: {
          ...state.queryResults,
          [action.payload.queryId]: {
            query: action.payload.query,
            result: action.payload.result,
          },
        },
      }
    default:
      return initialState
  }
}

function App() {
  
  const [state, dispatch] = useReducer(reducer, initialState)
    // Function to dispatch an action to update results
    const updateResults = (queryId, query, result) => {
      dispatch({
        type: ACTIONS.SET_QUERIES_DATA,
        payload: { queryId, query, result },
      });
    };
  
    useEffect(() => {
     
      const test = (queryArray) => {
        let gblQueryCnt = 0;
  
        const callback = (result, tmpObj) => {
          // Dispatch an action to update results
          updateResults(`query${tmpObj}`, queryArray[tmpObj-1], result);
        };
  
        for (let i = 0; i < queryArray.length; i++) {
          let q = queryArray[i];
          gblQueryCnt++;   
          asyncQueue(q, callback, gblQueryCnt , 'Tests');
        }
      };
  
      // Example test queries
      test([
        '(name of it, number of relevant fixlets of it) of bes computers',       
      ]);
    }, []); 
  
  return (
    <div className="App">
     <Layout state={state}  dispatch={dispatch}/>
   
    </div>
  );
}

export default App;
